  html {
    touch-action: manipulation;
  }
  
  body {
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  
  @keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
  }
  
  .animate-shake {
    animation: shake 0.5s;
    animation-iteration-count: infinite;
  }

  /* Custom Scrollbar Styles */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #f0f0f0; /* Background of the scrollbar track */
}

::-webkit-scrollbar-thumb {
  background-color: #FA891B; /* Orange color */
  border-radius: 10px;
  border: 3px solid #f0f0f0; /* Padding around the thumb */
}

::-webkit-scrollbar-thumb:hover {
  background-color: #e07a17; /* Darker shade of orange on hover */
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #FA891B #f0f0f0; /* Orange thumb, light gray track */
}

/* Edge, IE */
* {
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

/* Custom scrollbar for Edge and IE */
*::-ms-scrollbar {
  width: 12px;
}

*::-ms-scrollbar-track {
  background: #f0f0f0; /* Background of the scrollbar track */
}

*::-ms-scrollbar-thumb {
  background-color: #FA891B; /* Orange color */
  border-radius: 10px;
  border: 3px solid #f0f0f0; /* Padding around the thumb */
}

*::-ms-scrollbar-thumb:hover {
  background-color: #e07a17; /* Darker shade of orange on hover */
}


  
  
/* Floating animation */
@keyframes float {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  10% {
    transform: translateY(-15px);
    opacity: 0.8;
  }
  20% {
    transform: translateY(-30px);
    opacity: 0.6;
  }
  30% {
    transform: translateY(-45px);
    opacity: 0.5;
  }
  40% {
    transform: translateY(-60px);
    opacity: 0.4;
  }
  50% {
    transform: translateY(-90px);
    opacity: 0.3;
  }
  60% {
    transform: translateY(-120px);
    opacity: 0.3;
  }
  70% {
    transform: translateY(-160px);
    opacity: 0.2;
  }
  80% {
    transform: translateY(-200px);
    opacity: 0.1;
  }
  90% {
    transform: translateY(-300px);
    opacity: 0.0;
  }
  100% {
    transform: translateY(-400px);
    opacity: 0;
  }
}




